.banner_apropos {
    display: flex;
    width: 100%;
    justify-content: center;
}

.banner_apropos img {
    width: 100%;
    height: 223px;
    border-radius: 25px;
    object-fit: cover;
    filter: brightness(0.8);
}

@media all and (max-width: 500px) {

    .banner_apropos img {
        border-radius: 10px;
        height: 111px;
    }
    
}